.leaderboard {
    min-width: fit-content;
    width: 90%;
    box-shadow: 2px 2px 5px 2px rgba(129, 129, 129, 0.174);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  table {
    min-width: fit-content;
    width: 98%;
    box-sizing: border-box;
    border: 2px solid #e2e2e2;
    border-radius: 15px;
    padding: 10px;
    margin-bottom: 20px;
    border-collapse: separate;
    border-spacing: 10px;
  }
  
  td {
    border: 1px solid #e2e2e2;
    border-radius: 5px;
    padding: 5px 10px;
  }
  
  th {
    color: rgb(63, 63, 63);
    padding: 10px;
    border: 2px solid #FFC0CB;
    border-radius: 5px;
    background-color: #ffc0cb28;
  }
.leaderhead{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.leaderhead h2,.select{
    margin: 20px 20px;
}
.select{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0;
    border: 1px solid #bebebe;
    border-radius: 5px;
    background-color: #e2e2e2;
}
.slippage{
    padding: 0 10px;
    margin: 0;
}
.option{
    padding: 10px;
    margin: 0;
    border: none;
}
/* Default horizontal layout */
.my-table {
    display: table;
  }

  @media (max-width: 800px)
  {
    th{
        display: none;
    }
  }
  
  @media (max-width: 700px) {
    .my-table {
      display: block;
    }
  
    .my-table thead {
      display: none;
    }
  
    .my-table tbody {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }
  
    .my-table tbody tr {
      display: contents; /* Allows the cells to be children of the grid container */
    }
  
    .my-table td {
      display: block;
      padding: 10px;
      border: 1px solid #ccc;
      position: relative;
    }
  
    .my-table td::before {
      content: attr(data-label);
      font-weight: bold;
      display: block;
      margin-bottom: 5px;
    }
    .leaderhead{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .leaderhead h2,.select{
        margin: 10px;
    }
    .select{
        width: 90%;
    }
    .slippage{
        width: 50%;
    }
    .option{
        width: 50%;
    }

  }
  