.App {
  text-align: center;
  border: solid 5px aqua;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.App-header {
  text-align: left;
  padding: 20px;
  padding-left: 100px;
}

main {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navbar{
  width: 100%;
  height: 10rem;
  background-image: url(./components/navbar.png);
  background-repeat: no-repeat;
  background-size: cover;
}